.profile {
    /*max-width: var(--main-width-container);*/
    display: grid;
    align-items: center;
    grid-template-columns: [image] min-content [info] max-content [empty] auto [add-button] max-content [end];
    grid-template-areas:
        "image info . add";
    width: 100%;
    padding-bottom:50px;
}

@media (max-width: 756px) {
    .profile {
        /*max-width: var(--mobile-width-container);*/
        grid-template-columns: [main] 1fr [end];
        justify-items: center;

        grid-template-areas:
            "image"
            "info"
            "add";
    }


}