.navBar__info_visible {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    display: flex;
    gap: 24px;
    transition: all .2s linear;
}

@media screen and (max-width: 430px) {
    .navBar__info_visible {
        width: 100%;
        text-align: center;
        flex-direction: column;
        padding: 0 0 40px;
        gap: 18px;
        border-bottom: 1px solid #545454;

    }
}